/* eslint-disable react/no-did-mount-set-state */
import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import styled from 'styled-components'
import debounce from 'debounce'

function prepJson(json) {
  if (!json) {
    return ''
  }
  const jsonString = JSON.stringify(json, null, 4)
  const trimmed = jsonString.substring(1, jsonString.length -1)
  return trimmed
}

class TestReadBlock extends Component {

  constructor(props) {
    super(props)
    this.state = {
      mounted: false,
      codeValue: props.defaultCodeValue,
      result: props.defaultResult,
      loadingResult: false,
      error: null
    }

    this.processInput = debounce(((props.func) ? (code)=> props.func(code, this) : (() => {})).bind(this), 800).bind(this)

    this.render = this.render.bind(this)
    this.componentDidMount = this.componentDidMount.bind(this)
  }

  componentDidMount() {

    const refToComp = this

    if (typeof window !== `undefined`) {
      const ace = require('brace')
      console.log(ace)
      debugger
      require('brace/mode/javascript');
      require('brace/theme/monokai');

      const node = ReactDOM.findDOMNode(this.root);

      var editor = ace.edit(node);
      editor.getSession().setMode('ace/mode/javascript');
      editor.setTheme('ace/theme/monokai');
      // editor.setTheme('brace/theme/tomorrow_night');
      // editor.getSession().setMode("javascript");
      editor.setShowPrintMargin(false);
      editor.setHighlightActiveLine(false);
      editor.setFontSize(14);
      editor.renderer.setShowGutter(false);


      editor.getSession().on('change', function() {
        const newValue = editor.getValue()
        refToComp.setState({ codeValue: newValue })
        refToComp.processInput(newValue)
      });
      editor.setValue(refToComp.state.codeValue);
      // editor.setOptions({minLines: 25});
      // editor.setOptions({maxLines: 50});
    }

  }

  render() {
    const {
      loadingResult,
      error,
      result,
    } = this.state

      return (
        <TestCodeWrapper>
          <CodeRegion>
            <Titled>Live Editor</Titled>
            <div
              ref={(r)=> { this.root = r}}
              style={{
              width: '97%',
              flex: '100%',
              background: 'transparent',
              overflow: 'scroll'}}
            />
          </CodeRegion>
          <Results>
            <Titled>Result</Titled>
            <JsonPreview>
              {(() => {
                if (loadingResult) {
                  return <span>Loading...</span>
                }
                if (error) {
                  return <span style={{ color: 'red' }}>{error}</span>
                }
                return prepJson(result)
              })()}
            </JsonPreview>
          </Results>
        </TestCodeWrapper>
      )
    }
}

const Titled = styled.div`
  height: 40px;
  width: 100%;
  color: grey;
  background-color: #21242A;
  font-size: 17px;
  font-family: Roboto;
  padding-top: 9px;
  padding-left: 9px;
  text-transform: uppercase;
`

const TestCodeWrapper = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 370px;
  border-radius: 10px;
  overflow: hidden;
  @media screen and (max-width: 600px) {
    flex-direction: column;
    height: 400px;
    border: 1px #e2e2e2 solid
  }
 
 
`

const CodeRegion = styled.div`
  background-color: #29282c;
  flex: 70%; 
  display: flex;
  overflow: none;
  flex-direction: column;
  @media screen and (max-width: 600px) {
    border: none
    flex: 50%; 
  }
`

const Results = styled.div`
  flex: 40%;
  flex-direction: column;
  border-right: 2px solid #e2e2e2;
  border-bottom: 2px solid #e2e2e2;
  overflow: hidden;
  border-radius: 0px 10px 10px 0px;
  @media screen and (max-width: 600px) {
    flex: 80%;
    border-radius: 0px;
    border: none;
  }
`

const JsonPreview = styled.div`
  font-size: 15px;
  white-space: pre;
  height: calc(100% - 40px);
  overflow: scroll;
`

export default TestReadBlock



