import React from 'react'

export default function FAQ(props) {
  return (
    <div>
      <h4 style={{fontWeight: 400, textAlign: 'center', marginBottom: '-15px'}}>{props.question}</h4>
      <h5 style={{textAlign: 'center'}}>{props.children}</h5>
    </div>)
}



