export function testRead(current, componentRef) {

  componentRef.setState({loadingResult: true, error: null})

  fetch('https://webdemo.opticdev.com/demo/read', {
    method: 'post',
    headers: {
      "Content-Type": "text/plain"
    },
    "mode" : "cors",
    body: current
  }).then(response => {
    response.json().then((json) => {
      if (json.success) {
        componentRef.setState({result: json.value, loadingResult: false})
      } else {
        componentRef.setState({error: json.error, loadingResult: false})
      }

    })
  }).catch(err => console.log(err))

}



